/* --------------------------------------------------------------
 * = ,  show,  hide
 * ----------------------------------------------------------- */

/*  */
@media screen and (max-width:35.5em) {
    .pure-md-show{display:none}
    .pure-lg-show{display:none}
    .pure-sm-hide{display:none}
    .pure-sm-center{text-align:center;}
}
/*  */
@media screen and (max-width:48em) {
    .pure-sm-show{display:none}
    .pure-lg-show{display:none}
    .pure-md-hide{display:none}
    .pure-md-center{text-align:center;}
}
/*  */
@media screen and (max-width:64em) {
    .pure-sm-show{display:none}
    .pure-md-show{display:none}
    .pure-lg-hide{display:none}
}
/*  */
@media screen and (min-width: 80em) {
    .pure-sm-show{display:none}
    .pure-md-show{display:none}
    .pure-xl-hide{display:none}
}